        </el-table-column>
      </el-table>

      <el-col :span="24" class="toolbar" ></el-col>
      <div style="height:50px;"></div>

      <el-row class="toolbar">
        <el-col :span="12">
          <el-button type="primary" @click="createFormShow" size="small">{{this.$parent.lan.ADD_TAG}}</el-button>
        </el-col>
        <el-col :span="12">
          <!--<el-pagination layout="total, sizes, prev, pager, next, jumper" @size-change="f_TableSizeChange" @current-change="f_TableCurrentChange"
                         :page-sizes="[10, 20, 50, 100]" :current-page="page" :page-size="page_size" :total="total" style="float: right"></el-pagination>-->
        </el-col>
      </el-row>

      <el-table :data="listTables" highlight-current-row width="100%" style="width: 100%;max-width:100%;" size="mini" border v-loading="listLoading">
        <el-table-column type="index" label=" " width="60"></el-table-column>
        <el-table-column prop="caption" :label="$parent.lan.MAIL_TAG">
          <template slot-scope="scope">
            <div>
              <i style="display:inline-block;width:10px;height:10px;" :style="{background:scope.row.color}"></i>
              {{scope.row.name}}
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="unseen_count" :label="$parent.lan.MAILBOX_COM_INNERBOX_UNREAD_MAIL"></el-table-column>
        <el-table-column prop="count" :label="$parent.lan.ALL_MAILS"></el-table-column>
        <el-table-column :label="$parent.lan.COMMON_OPRATE" width="280">
          <template slot-scope="scope">
            <el-button type="info" size="mini" @click="updateFormShow(scope.$index, scope.row)">{{$parent.lan.COMMON_BUTTON_ALTER}}</el-button>
            <el-button type="danger" size="mini" @click="deleteRow(scope.$index, scope.row)">{{$parent.lan.COMMON_BUTTON_DELETE}}</el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-col :span="24" class="toolbar"></el-col>

      <!--新增 标签-->
      <el-dialog :title="$parent.lan.ADD_TAG"  :visible.sync="createFormVisible" :close-on-click-modal="false" :append-to-body="true">
        <el-form :model="createForm" label-width="100px" :rules="createFormRules" ref="createForm">
          <el-form-item :label="$parent.lan.TAG_NAME" prop="name">
            <el-input v-model.trim="createForm.name" auto-complete="off"></el-input>
          </el-form-item>

          <el-form-item :label="$parent.lan.TAG_COLOR" prop="color">
            <el-color-picker
              v-model="createForm.color" :predefine="colors"
              >
            </el-color-picker>
            <!--<el-button @click="create_show_color = !create_show_color">-->
              <!--<i style="display:inline-block;width:12px;height:12px;" :style="{background:createForm.color}"></i>-->
              <!--{{$parent.lan.PICK_COLOR}}-->
              <!--<i class="el-icon-caret-bottom"></i>-->
            <!--</el-button>-->
            <!--<ul v-show="create_show_color" class="clear" style="width:200px;border:1px solid #ccc;box-shadow: 0 0 5px #ccc;padding-left:10px;padding-top:10px;">-->
              <!--<li v-for="(c,k) in predefineColors" :key="k" @click="pickColor(c)" style="float:left;margin-right:10px;">-->
                <!--<i style="display:inline-block;width:20px;height:20px;cursor: pointer" :style="{background:c.color}" :title="c.title"></i>-->
              <!--</li>-->
            <!--</ul>-->
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="createFormVisible = false">{{$parent.lan.COMMON_BUTTON_CANCELL}}</el-button>
          <el-button type="primary" @click.native="createFormSubmit()" :loading="createFormLoading">{{$parent.lan.COMMON_BUTTON_SUBMIT}}</el-button>
        </div>
      </el-dialog>

      <!--更新 标签-->
      <el-dialog :title="$parent.lan.EDIT_TAG"  :visible.sync="updateFormVisible" :close-on-click-modal="false" :append-to-body="true">
        <el-form :model="updateForm" label-width="100px" :rules="updateFormRules" ref="updateForm">
          <el-form-item :label="$parent.lan.TAG_NAME" prop="name">
            <el-input v-model.trim="updateForm.name" auto-complete="off"></el-input>
          </el-form-item>

          <el-form-item :label="$parent.lan.TAG_COLOR" prop="color">
            <el-color-picker
              v-model="updateForm.color" :predefine="colors"
              >
            </el-color-picker>
            <!--<el-button @click="update_show_color = !update_show_color">-->
              <!--<i style="display:inline-block;width:12px;height:12px;" :style="{background:updateForm.color}"></i>-->
              <!--{{$parent.lan.PICK_COLOR}}-->
              <!--<i class="el-icon-caret-bottom"></i>-->
            <!--</el-button>-->
            <!--<ul v-show="update_show_color" class="clear" style="width:200px;border:1px solid #ccc;box-shadow: 0 0 5px #ccc;padding-left:10px;padding-top:10px;">-->
              <!--<li v-for="(c,k) in predefineColors" :key="k" @click="pickColor_update(c)" style="float:left;margin-right:10px;">-->
                <!--<i style="display:inline-block;width:20px;height:20px;cursor: pointer" :style="{background:c.color}" :title="c.title"></i>-->
              <!--</li>-->
            <!--</ul>-->
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="updateFormVisible = false">{{$parent.lan.COMMON_BUTTON_CANCELL}}</el-button>
          <el-button type="primary" @click.native="updateFormSubmit()" :loading="updateFormLoading">{{$parent.lan.COMMON_BUTTON_SUBMIT}}</el-button>
        </div>
      </el-dialog>

      <el-dialog :title="$parent.lan.MAILBOX_NEW_FOLDER" :visible.sync="dialogFormVisible" :close-on-click-modal="false" :append-to-body="true">
        <el-form :model="form" :rules="rules" ref="ruleForm">
          <el-form-item :label="this.$parent.lan.MAILBOX_FOLDER_NAME" label-width="120px" prop="name">
            <el-input v-model="form.name" auto-complete="off"></el-input>
          </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">{{this.$parent.lan.cancel}}</el-button>
          <el-button type="primary" @click="append">{{this.$parent.lan.sure}}</el-button>
        </div>
      </el-dialog>

      <el-dialog :title="$parent.lan.FILE_P_RENAME" :close-on-click-modal="false" :visible.sync="renameFormVisible" :append-to-body="true">
        <el-form :model="renameForm" :rules="renameRules" ref="renameForm">
          <el-form-item :label="$parent.lan.MAILBOX_FOLDER_NAME" label-width="120px" prop="dest_name">
            <el-input v-model="renameForm.dest_name" auto-complete="off"></el-input>
          </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="cancel_rename">{{this.$parent.lan.cancel}}</el-button>
          <el-button type="primary" @click="renameSubmit">{{this.$parent.lan.sure}}</el-button>
        </div>
      </el-dialog>

      <!--删除文件夹-->
      <el-dialog :title="$parent.lan.DELETE_FOLDER" :close-on-click-modal="false" :visible.sync="delFolderVisible" :append-to-body="true" width="500px">
        <el-form :model="delFolderForm"  ref="delFolderForm" size="small">
          <el-form-item label=""  >
            <b style="font-size:16px;">{{$parent.lan.IS_DELETE_FOLDER}} “{{delFolderForm.name}}”？</b>
          </el-form-item>
          <el-form-item label="" prop="dst_folder" v-if="!delFolderForm.is_delete">
            <span>{{$parent.lan.MAIL_MOVE_TO}}:</span>
            <el-select v-model="delFolderForm.dst_folder" :placeholder="$parent.lan.CHOOSE_FOLDER">
              <el-option v-for="item in moveItems" :label="item.text" :value="item.id" :key="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=""  v-if="delFolderForm.is_delete">
            <span style="color:red;">{{$parent.lan.CLEAR_FOLDER_DESC}}</span>
          </el-form-item>
          <el-form-item label=""  prop="is_delete">
            <el-checkbox v-model="delFolderForm.is_delete">{{$parent.lan.ALSO_DEL_MAIL}}</el-checkbox>
          </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="delFolderVisible = false" size="small">{{this.$parent.lan.cancel}}</el-button>
          <el-button type="primary" @click="folderDel" size="small">{{this.$parent.lan.sure}}</el-button>
        </div>
      </el-dialog>
    </section>
  </div>
</template>
<script>
  import {MessageBox} from 'element-ui';
  import {getMailTag, creatMailTag, getSingleMailTag, updateMailTag, deleteMailTag, folderExt, creatFolder, deleteFolder,messageExpunge, folderRename, getMailTagExt,folderDel} from '@/api/api'

  export default {
    data() {
      let _self = this;
      return {
        colors: [
          "#D50000",
          "#E67C73",
          "#F4511E",
          "#F6BF26",
          "#33B679",
          "#0B8043",
          "#039BE5",
          "#3F51B5",
          "#7986CB",
          "#8E24AA",
          "#616161",
          "#458138",
          "#4680BF",
          "#FFE2E6",
          "#74CAE5",
          "#818181"
        ],
        delFolderVisible:false,
        delFolderForm:{
          src_folder:'',
          name:'',
          dst_folder:'INBOX',
          is_delete:false
        },
        sels:[],
        renameFormVisible:false,
        renameForm:{
          dest_name:'',
          src_raw_name:''

        },
        renameRules:{
          dest_name:[
            { required: true, message: _self.$parent.lan.FILE_P_RULE1, trigger: 'blur' },
            { min: 1, max: 20, message: _self.$parent.lan.FILE_P_RULE2, trigger: 'blur' }
          ]
        },
        dialogFormVisible:false,
        form:{
          name:''
        },
        rules:{
          name:[
            { required: true, message: _self.$parent.lan.FILE_P_RULE1, trigger: 'blur' },
            { min: 1, max: 20, message: _self.$parent.lan.FILE_P_RULE2, trigger: 'blur' }
          ]
        },
        listLoading_folder:false,
        listTables_folder: [],
        update_show_color:false,
        create_show_color:false,
        predefineColors:[
          {color:"#D50000", title:_self.$parent.lan.CALENDAR_PAGE_CAL_TOMATO_RED},
          {color:"#E67C73", title:_self.$parent.lan.CALENDAR_PAGE_CAL_RED_CRANE_COLOR},
          {color:"#F4511E", title:_self.$parent.lan.CALENDAR_PAGE_CAL_TANGERINE},
          {color:"#F6BF26", title:_self.$parent.lan.CALENDAR_PAGE_CAL_BANANA_YELLOW},
          {color:"#33B679", title:_self.$parent.lan.CALENDAR_PAGE_CAL_SALVIA_GREEN},
          {color:"#0B8043", title:_self.$parent.lan.CALENDAR_PAGE_CAL_BASIL_GREEN},
          {color:"#039BE5", title:_self.$parent.lan.CALENDAR_PAGE_CAL_PEACOCK_BLUE},
          {color:"#3F51B5", title:_self.$parent.lan.CALENDAR_PAGE_CAL_BLUEBERRY_COLOR},
          {color:"#7986CB", title:_self.$parent.lan.CALENDAR_PAGE_CAL_LAVENDER},
          {color:"#8E24AA", title:_self.$parent.lan.CALENDAR_PAGE_CAL_GRAPE_PURPLE},
          {color:"#616161", title:_self.$parent.lan.CALENDAR_PAGE_CAL_GRAPHITE_BLACK},
          {color:"#458138", title:_self.$parent.lan.SETTING_SKIN_QINGXINLU},
          {color:"#4680BF", title:_self.$parent.lan.SETTING_SKIN_TIANKONGLAN},
          {color:"#FFE2E6", title:_self.$parent.lan.SETTING_SKIN_TAOHUAHONG},
          {color:"#74CAE5", title:_self.$parent.lan.SETTING_SKIN_HAISHUILAN},
          {color:"#818181", title:_self.$parent.lan.DARK_GRAY},

        ],
        listLoading: false,
        listTables: [],
        createFormVisible: false,
        createFormLoading: false,
        createForm: {name: '', color: '#33B679'},
        createFormRules: {
          name: [
            { required: true, message: _self.$parent.lan.TAG_NAME_RULE, trigger: 'blur' },
            { min: 1, max: 20, message: _self.$parent.lan.FILE_P_RULE2, trigger: 'blur' }
          ],
          color: [
            { required: true, message: _self.$parent.lan.please_choose, trigger: 'blur' },
          ],
        },

        updateFormVisible: false,
        updateFormLoading: false,
        updateForm: {name: '', color: ''},
        updateFormRules: {
          name: [
            { required: true, message: _self.$parent.lan.TAG_NAME_RULE, trigger: 'blur' },
            { min: 1, max: 20, message: _self.$parent.lan.FILE_P_RULE2, trigger: 'blur' }
          ],
          color: [
            { required: true, message: _self.$parent.lan.please_choose, trigger: 'blur' }
          ],
        },

      }
    },
    mounted: function () {
      this.getTables();
      this.getTables_folder();
    },
    methods: {
      folderDel(){
        let param = {
          src_folder: this.delFolderForm.src_folder,
          is_delete: this.delFolderForm.is_delete
        }
        if(!this.delFolderForm.is_delete){
          param.dst_folder = this.delFolderForm.dst_folder
        }
        folderDel(param).then(res=>{
          this.delFolderVisible = false;
          this.getTables_folder();
          if(sessionStorage['mailbox_url'].indexOf('/mailbox/innerbox/'+this.delFolderForm.src_folder)>=0){
            sessionStorage['mailbox_url'] = '/mailbox/innerbox/INBOX'
          }
          this.$root.$children[0].$children[0].$refs.mailbox_ele.getFloderfn()
          this.$message({
            type: 'success',
            message: this.$parent.lan.COMMON_OPRATE_SUCCESS
          });
        }).catch(err=>{
          console.log(err);
        })
      },
      cancel_rename(){
        this.renameFormVisible = false;
        this.$refs['renameForm'].clearValidate();
      },
      show_rename_dialog(index,row){
        this.renameForm.dest_name = row.name;
        this.renameForm.src_raw_name = row.raw_name;
        this.renameFormVisible = true;
      },
      renameSubmit(){
        this.$refs['renameForm'].validate((valid) => {
          if (valid) {
            folderRename({"dest_name":this.renameForm.dest_name,"src_raw_name":this.renameForm.src_raw_name}).then((res)=>{
              this.getTables_folder();
              if(sessionStorage['mailbox_url'].indexOf('/mailbox/innerbox/'+this.renameForm.src_raw_name)>=0){
                sessionStorage['mailbox_url'] = '/mailbox/innerbox/'+res.data.raw_name
              }
              this.$root.$children[0].$children[0].$refs.mailbox_ele.getFloderfn()
              this.$message({
                type: 'success',
                message: this.$parent.lan.COMMON_ALTER_SUCCESS
              });
              this.renameFormVisible = false;
            }).catch((err)=>{
              let str = '';
              if(err){
                str = err;
              }
              if(err.detail){

              }else if(err.non_field_errors){

              }else if(err.dest_name){
                str = err.dest_name[0];
                this.$message({
                  type: 'error',
                  message:  str
                });
              }else{
                if(str && str.length>0){
                  this.$message({
                    type: 'error',
                    message:  str
                  });
                }

              }
              console.log(err);
            })
          } else {
            return false;
          }
        });
      },
      empty_folder(index,row){
        let param = {
          folder:row.raw_name
        }
        this.$confirm( this.$parent.lan.MAILBOX_COM_INNERBOX_DELETE_DESC , this.$parent.lan.COMMON_BUTTON_CONFIRM_NOTICE, {
            confirmButtonText: this.$parent.lan.COMMON_BUTTON_CONFIRM,
            cancelButtonText: this.$parent.lan.COMMON_BUTTON_CANCELL,
            type: 'warning'
          }).then(() => {
            messageExpunge(param).then(res=>{
              this.$message(
                {type:'success',message:this.$parent.lan.COMMON_OPRATE_SUCCESS}
              )
              this.getTables_folder();
              this.$root.$children[0].$children[0].$refs.mailbox_ele.getFloderfn()
            }).catch(err=>{
              console.log(err);
            })
          }).catch(() => {

          });
      },
      append(){
        this.$refs['ruleForm'].validate((valid) => {
          if (valid) {
            creatFolder({"name":this.form.name}).then((suc)=>{
              this.getTables_folder();
              this.$root.$children[0].$children[0].$refs.mailbox_ele.getFloderfn()
              this.form.name='';
              this.$message({
                type: 'success',
                message: this.$parent.lan.COMMON_ADD_SUCCESS
              });
              this.dialogFormVisible = false;
            }).catch((err)=>{
              let str = '';
              if(err){
                str = err;
              }
              if(err.non_field_errors){

              }else if(err.detail){

              }else if(err.error_message){
                str = err.error_message;
                this.$message({
                  type: 'error',
                  message:  str
                });
              }else  if(err.name){
                str = err.name[0]
                this.$message({
                  type: 'error',
                  message:  str
                });
              }else{
                if(str && str.length>0){
                  this.$message({type: 'error',message:  str });
                }
              }
              console.log(err);
            })
          } else {
            return false;
          }
        });

      },
      createFormShow_folder(){
        this.dialogFormVisible = true;
      },
      pickColor_update(c){
        this.updateForm.color = c.color;
        this.update_show_color = false;
      },
      pickColor(c){
        this.createForm.color = c.color;
        this.create_show_color = false;
      },
      getTables: function(){
        this.listLoading = true;
        getMailTagExt().then(res=>{
          this.listTables = res.data;
          this.listLoading = false;
        }).catch((err)=>{
          console.log(err)
          let str = '';
          if(err){
            str = err;
          }
          if(err.non_field_errors){

          }else if(err.detail){

          }else if(err.error_message){
            str = err.error_message;
            this.$message({
             message: str,
             type:'error'
            })
          }else{
            if(str && str.length>0){
              this.$message({
               message: str,
               type:'error'
              })
            }

          }
          this.listLoading = false;
        });
      },
      getTables_folder(){
        this.listLoading_folder = true;
        folderExt().then(res=>{
          this.listTables_folder = res.data;
          this.listLoading_folder = false;
        }).catch(err=>{
          let str = '';
          if(err){
            str = err;
          }
          if(err.non_field_errors){

          }else if(err.detail){

          }else if(err.error_message){
            str = err.error_message;
            this.$message({
             message: str,
             type:'error'
            })
          }else{
            if(str && str.length>0){
              this.$message({
               message: str,
               type:'error'
              })
            }
          }
          this.listLoading_folder = false;
        })
      },
      createFormShow: function(){
        this.createForm = Object.assign({}, this.createForm);
        this.create_show_color = false;
        this.createFormLoading = false;
        this.createFormVisible = true;
      },
      createFormSubmit: function(){
        this.$refs.createForm.validate((valid) => {
          if (valid) {
            this.createFormLoading = true;
            let para = Object.assign({}, this.createForm);
            creatMailTag(para)
            .then((res) => {
              this.$message({message: this.$parent.lan.COMMON_ADD_SUCCESS, type: 'success'});
              this.$refs['createForm'].resetFields();
              this.createFormVisible = false;
              this.createFormLoading = false;
              this.getTables();
              if(this.$root.$children[0].$children[0].$refs.mailbox_ele.$refs['innerbox']){
                this.$root.$children[0].$children[0].$refs.mailbox_ele.$refs['innerbox'][0].getMailTag();
              }
            })
            .catch( (err)=> {
              console.log(err.name)
              let str = '';
              str = err;
              if(err.name){
                str = err.name[0];
              }
              if(str && str.length>0){
                this.$message({ message: str,  type: 'error' });
              }
              this.createFormLoading = false;
            });
          }
        });
      },
      updateFormShow: function (index, row) {
        this.update_show_color = false;
        getSingleMailTag(row.id).then(res=>{
          let form = Object.assign({}, res.data);
          this.updateForm = form;
          this.updateFormVisible = true;
          this.updateFormLoading = false;
        });
      },
      updateFormSubmit: function(){
        this.$refs.updateForm.validate((valid) => {
          if (valid) {
            this.updateFormLoading = true;
            let para = Object.assign({}, this.updateForm);
            updateMailTag(para.id, para)
            .then((res) => {
              this.updateFormLoading = false;
              this.updateFormVisible = false;
              this.$message({message: this.$parent.lan.COMMON_ALTER_SUCCESS, type: 'success'});
              this.getTables();
              if(this.$root.$children[0].$children[0].$refs.mailbox_ele.$refs['innerbox']){
                this.$root.$children[0].$children[0].$refs.mailbox_ele.$refs['innerbox'][0].getMailTag();
              }
            })
            .catch( (err)=> {
              this.updateFormLoading = false;
              let str = '';
              str = err;
              if(err.name){
                str = err.name[0];
              }
              if(str && str.length>0){
                this.$message({ message: str,  type: 'error' });
              }
            });
          }
        });
      },

      deleteRow: function (index, row) {
        let that = this;
        this.$confirm(this.$parent.lan.COMMON_BUTTON_DELETE_SUBMIT, this.$parent.lan.COMMON_BUTTON_CONFIRM_NOTICE, {
          type: 'warning'
        }).then(() => {
          deleteMailTag(row.id)
            .then((res)=> {
              that.$message({ message: this.$parent.lan.COMMON_DELETE_SUCCESS, type: 'success' });
              this.getTables();
              if(this.$root.$children[0].$children[0].$refs.mailbox_ele.$refs['innerbox']){
                this.$root.$children[0].$children[0].$refs.mailbox_ele.$refs['innerbox'][0].getMailTag();
              }
            })
            .catch( (err)=> {
              let str = '';
              str = err ;
              if(err.detail){

              }else if(err.non_field_errors){

              }else{
                if(str && str.length>0){
                  that.$message({ message: str,  type: 'error' });
                }
              }

            });
        });
      },
      deleteRow_folder: function (index, data) {
        this.delFolderForm.name = data.name;
        this.delFolderForm.src_folder = data.raw_name;
        this.delFolderForm.dst_folder = 'INBOX';
        this.delFolderForm.is_delete = false;
        this.delFolderVisible = true;
      },
    },
    computed:{
      moveItems:function(){
        let folder = this.listTables_folder;
        let arr = [];
        for(let i=0;i<folder.length;i++){
          if(folder[i]['raw_name']!='Drafts'&&folder[i]['raw_name']!=this.boxId){
            let obj={};
            obj['text'] = folder[i]['name'];
            obj['id'] = folder[i]['raw_name'];
            arr.push(obj);
          }
        }
        return arr;
      },
    },
  }
</script>



